* {
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: -apple-system;
  min-height: 100vh;
  padding: 0;
  background-color: burlywood;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
  padding: 1.5rem;
  
}

.nav {
  border-bottom: 3px solid black;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 30px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 12px 16px;
}

nav a:hover {
  background: black;
  color: white;
}

.articlelist-cont {
  border-bottom: 1px solid black;
}

.articlelist-link {
  text-decoration: none;
}

.articlelist-paragraph {
  font-size: .9rem;
  color: black;
  font-weight: 200;

}

.comment {
  border-bottom: 2px solid black;
  padding: 6px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 6px;
}

.comment p {
  margin-top: 0;
}

.comment h4 {
  margin-bottom: .4rem;
}

.upvotes-section button, p {
  display: inline-block;
  margin-right: 18px;
}

button {
  padding: .5rem 1.3rem;
  border-radius: 5px;
  border: none;
  border-color: burlywood;
  outline: none;
  font-weight: bold;
  cursor: pointer;
}

button:hover {
  color: white;
  background-color: black;
}

.add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
  
}

.input-container {
  width: 320px;
  margin: auto auto;
}

.add-comment-form h3 {
  margin-bottom: 16px;
}

.input-container input, textarea {
  display: block;
  width: 320px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

.add-comment-form button {
  width: 320px;
}


footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  max-width: 700px;
  border-top: 3px solid black;
}
